import "core-js/modules/es.array.push.js";
import cookies from 'vue-cookies';
import axios from '@/utils/axios';
import { mapGetters } from 'vuex';
import BaseLogin from '@/components/BaseLogin.vue';
export default {
  data() {
    return {
      loginFields: [{
        key: 'password',
        placeholder: 'Please enter the authentication code',
        type: 'password',
        showPassword: true
      }]
    };
  },
  computed: {
    ...mapGetters(['userConfig']),
    ownerName() {
      return this.userConfig?.ownerName || 'OIMG';
    },
    loginTitle() {
      return `Login ${this.ownerName}`;
    }
  },
  components: {
    BaseLogin
  },
  methods: {
    handleLogin(formData) {
      const {
        password
      } = formData;
      const writtenPass = password === '' ? 'unset' : password;
      axios.post('/api/login', {
        authCode: password
      }).then(res => {
        if (res.status !== 200) {
          this.$message.error('登录失败，请检查密码是否正确');
          return;
        }
        cookies.set('authCode', writtenPass, '14d');
        this.$router.push('/');
        this.$message.success('登录成功');
      }).catch(err => {
        this.$message.error('登录失败，请检查密码是否正确');
      });
    }
  }
};