import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.for-each.js";
import Footer from '@/components/Footer.vue';
import ToggleDark from '@/components/ToggleDark.vue';
import Logo from '@/components/Logo.vue';
import { mapGetters } from 'vuex';
import backgroundManager from '@/mixins/backgroundManager';
export default {
  name: 'BaseLogin',
  mixins: [backgroundManager],
  props: {
    // 页面标题
    title: {
      type: String,
      required: true
    },
    // 输入字段配置
    fields: {
      type: Array,
      required: true
      // fields 格式: [{ key: 'username', label: '用户名', placeholder: '请输入用户名', type: 'text', showPassword: false }]
    },
    // 提交按钮文本
    submitText: {
      type: String,
      default: '登录'
    },
    // 背景图配置键名
    backgroundKey: {
      type: String,
      required: true
    },
    // 是否为管理端登录（影响背景样式）
    isAdmin: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      formData: {}
    };
  },
  computed: {
    ...mapGetters(['userConfig'])
  },
  components: {
    Footer,
    ToggleDark,
    Logo
  },
  mounted() {
    // 初始化表单数据
    this.initFormData();
    // 初始化背景图
    this.initializeBackground(this.backgroundKey, '.login', !this.isAdmin, true);
  },
  methods: {
    initFormData() {
      // 根据字段配置初始化表单数据
      const newFormData = {};
      this.fields.forEach(field => {
        newFormData[field.key] = '';
      });
      this.formData = newFormData;
    },
    handleSubmit() {
      // 触发父组件的提交事件，传递表单数据
      this.$emit('submit', {
        ...this.formData
      });
    },
    handleInputFocus(event) {
      const container = event.target.closest('.input-container');
      if (container) {
        const wrapper = container.querySelector('.input-wrapper');
        if (wrapper) {
          wrapper.classList.add('focused');
        }
      }
    },
    handleInputBlur(event) {
      const container = event.target.closest('.input-container');
      if (container) {
        const wrapper = container.querySelector('.input-wrapper');
        if (wrapper) {
          wrapper.classList.remove('focused');
        }
      }
    }
  }
};